import React, { Suspense } from 'react'
import Hero from './features/hero/Hero.component.jsx'
import Projects from './features/projects/Projects.component.jsx'
import Certificates from './features/certificates/Certificates.component.jsx'
import Contact from './features/contact/Contact.component.jsx'
import LoadingSystem from './systems/loader/Loading.system.jsx'
import CursorSystem from './systems/cursor/Cursor.system.jsx'
import NavDots from './ui/navigation/NavDots.jsx'
import HeroScene from './features/hero/Hero.scene.jsx'
import './App.css'
import { ThreeProvider } from './core/providers/ThreeProvider.jsx'

// Error Boundary Component
class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props)
    this.state = { hasError: false, error: null }
  }

  static getDerivedStateFromError(error) {
    return { hasError: true, error }
  }

  componentDidCatch(error, errorInfo) {
    console.error('Error caught by boundary:', error, errorInfo)
  }

  render() {
    if (this.state.hasError) {
      return (
        <div style={{ 
          padding: '20px', 
          textAlign: 'center', 
          color: 'white',
          background: '#121212',
          height: '100vh',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center'
        }}>
          <h2>Something went wrong</h2>
          <p>Please refresh the page to try again.</p>
          <button 
            onClick={() => window.location.reload()}
            style={{
              padding: '10px 20px',
              background: '#667eea',
              border: 'none',
              borderRadius: '5px',
              color: 'white',
              cursor: 'pointer'
            }}
          >
            Refresh Page
          </button>
        </div>
      )
    }

    return this.props.children
  }
}

function App() {
  return (
    <ErrorBoundary>
      <div className="app">
        <CursorSystem />
        <LoadingSystem />
        
        <main className="main-content">
          <Hero />
          <Projects />
          <Certificates />
          <Contact />
        </main>
        
        <NavDots />
        
        <ThreeProvider>
          <Suspense fallback={null}>
            <HeroScene />
          </Suspense>
        </ThreeProvider>
      </div>
    </ErrorBoundary>
  )
}

export default App 