import React, { useRef } from 'react'
import { motion, useInView } from 'framer-motion'
import ProjectCard from './ProjectCard/Card.component.jsx'
import './Projects.styles.css'

const projectsData = [
  {
    id: 1,
    title: "E-Commerce Platform",
    description: "Full-stack e-commerce solution with React, Node.js, and MongoDB",
    technologies: ["React", "Node.js", "MongoDB", "Stripe"],
    image: "/assets/media/project1.jpg",
    link: "#",
    github: "#"
  },
  {
    id: 2,
    title: "AI Chat Application",
    description: "Real-time chat app with AI integration and voice recognition",
    technologies: ["Python", "TensorFlow", "WebRTC", "Socket.io"],
    image: "/assets/media/project2.jpg",
    link: "#",
    github: "#"
  },
  {
    id: 3,
    title: "3D Portfolio Website",
    description: "Interactive 3D portfolio with Three.js and React Three Fiber",
    technologies: ["Three.js", "React", "Framer Motion", "GSAP"],
    image: "/assets/media/project3.jpg",
    link: "#",
    github: "#"
  },
  {
    id: 4,
    title: "Mobile Fitness App",
    description: "Cross-platform fitness tracking app with social features",
    technologies: ["React Native", "Firebase", "Redux", "Expo"],
    image: "/assets/media/project4.jpg",
    link: "#",
    github: "#"
  },
  {
    id: 5,
    title: "Blockchain Dashboard",
    description: "Real-time cryptocurrency dashboard with trading features",
    technologies: ["Vue.js", "Web3.js", "D3.js", "Ethereum"],
    image: "/assets/media/project5.jpg",
    link: "#",
    github: "#"
  }
]

const Projects = () => {
  const ref = useRef(null)
  const isInView = useInView(ref, { once: true, margin: "-100px" })

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        duration: 0.8,
        staggerChildren: 0.2
      }
    }
  }

  const itemVariants = {
    hidden: { y: 50, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.6,
        ease: "easeOut"
      }
    }
  }

  return (
    <section ref={ref} className="projects-section">
      <motion.div 
        className="projects-container"
        variants={containerVariants}
        initial="hidden"
        animate={isInView ? "visible" : "hidden"}
      >
        <motion.div className="section-header" variants={itemVariants}>
          <h2 className="section-title">
            Featured <span className="gradient-text">Projects</span>
          </h2>
          <p className="section-subtitle">
            A collection of my latest work showcasing creativity and technical expertise
          </p>
        </motion.div>

        <motion.div className="projects-grid" variants={itemVariants}>
          {projectsData.map((project, index) => (
            <ProjectCard 
              key={project.id} 
              project={project} 
              index={index}
            />
          ))}
        </motion.div>

        <motion.div className="projects-cta" variants={itemVariants}>
          <button className="view-all-button">
            <span>View All Projects</span>
            <div className="button-arrow">‚Üí</div>
          </button>
        </motion.div>
      </motion.div>

      <div className="projects-background">
        <div className="floating-shapes">
          {[...Array(6)].map((_, i) => (
            <div 
              key={i} 
              className="floating-shape"
              style={{
                '--delay': `${i * 0.5}s`,
                '--duration': `${3 + i * 0.5}s`
              }}
            ></div>
          ))}
        </div>
      </div>
    </section>
  )
}

export default Projects 