import React, { useRef } from 'react'
import { motion } from 'framer-motion'
import { useInView } from 'framer-motion'
import './Hero.styles.css'

const Hero = () => {
  const ref = useRef(null)
  const isInView = useInView(ref, { once: true })

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        duration: 0.8,
        staggerChildren: 0.2
      }
    }
  }

  const itemVariants = {
    hidden: { y: 50, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.6,
        ease: "easeOut"
      }
    }
  }

  return (
    <section ref={ref} className="hero-section">
      <div className="hero-background">
        {/* Three.js scene is rendered separately in App.jsx */}
      </div>
      
      <motion.div 
        className="hero-content"
        variants={containerVariants}
        initial="hidden"
        animate={isInView ? "visible" : "hidden"}
      >
        <motion.div className="hero-text" variants={itemVariants}>
          <h1 className="hero-title">
            <span className="gradient-text">Creative</span> Developer
          </h1>
          <p className="hero-subtitle">
            Crafting digital experiences with code, design, and innovation
          </p>
        </motion.div>

        <motion.div className="hero-actions" variants={itemVariants}>
          <button className="cta-button primary">
            <span>View Projects</span>
            <div className="button-glow"></div>
          </button>
          <button className="cta-button secondary">
            <span>Get In Touch</span>
          </button>
        </motion.div>

        <motion.div className="hero-scroll-indicator" variants={itemVariants}>
          <div className="scroll-arrow">
            <div className="arrow-line"></div>
            <div className="arrow-head"></div>
          </div>
          <span>Scroll to explore</span>
        </motion.div>
      </motion.div>

      <div className="hero-particles">
        {[...Array(20)].map((_, i) => (
          <div key={i} className="particle" style={{
            '--delay': `${Math.random() * 3}s`,
            '--duration': `${2 + Math.random() * 2}s`
          }}></div>
        ))}
      </div>
    </section>
  )
}

export default Hero 